<template>
<div class="login">
  <form
    class="login__form"
    @submit.prevent="submit()"
  >
    <div class="login__form-row">
      <label
        class="login__form-label"
        for="login"
      >login</label>
      <input
        type="text"
        id="login"
        required
        v-model.lazy="model.login"
      />
    </div>
    <div class="login__form-row">
      <label
        class="login__form-label"
        for="pass"
      >password</label>
      <input
        type="password"
        id="pass"
        required
        v-model.lazy="model.password"
      />
    </div>
    <button
      class="login__form-send"
      type="submit"
    >log in</button>
  </form>
  <p>{{ user }}</p>
</div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'login',
  data () {
    return {
      user: this.$store.state.user,
      loggIn: this.$store.state.loggIn,
      response: {
        status: ''
      },
      model: {
        login: '',
        password: ''
      }
    }
  },
  methods: {
    ...mapActions(['auth']),
    ...mapGetters(['isLoggIn']),
    submit () {
      this.auth(this.model)
    }
  },
  computed: {
  },
  route: {
    activate () {

    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../static/default.css';
.login {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100vh;
  &__form {
    box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14),0 6px 30px 5px rgba(0,0,0,0.12),0 8px 10px -5px rgba(0,0,0,0.3);
    min-width: 290px;
    padding: 10px;
    &-row {
      display: flex;
      flex-direction: column;
    }
    &-label {}
    &-send {
      text-decoration: none;
      color: #fff;
      background-color: #26a69a;
      text-align: center;
      letter-spacing: .5px;
      -webkit-transition: .2s ease-out;
      transition: .2s ease-out;
      cursor: pointer;
      border: none;
      border-radius: 2px;
      display: inline-block;
      height: 36px;
      line-height: 36px;
      padding: 0 2rem;
      text-transform: uppercase;
      vertical-align: middle;
      box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14),0 1px 5px 0 rgba(0,0,0,0.12),0 3px 1px -2px rgba(0,0,0,0.2);
    }
  }
}
</style>



