<template>
<div tag="main">
  User main page if logIN
  <button @click="logout">log out</button>
</div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'me',
  methods: {
    ...mapActions(['logOut']),
    logout () {
      this.logOut()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>




