import axios from 'axios'
// import router from '../../router/index'

const state = {
  user: null,
  loggIn: false
}

const mutations = {
  userData (state, udata) {
    state.user = udata
  },
  loggInUser (state, isLogg) {
    state.loggIn = isLogg
  }
}

const actions = {
  auth ({ commit }, { login, password }) {
    axios.post('https://mix.sumdu.edu.ua/api/v1/party/login?login=' + login + '&password=' + password, {
      withCredentials: true
    }).then((res) => {
      state.isLogg = true
      localStorage.setItem('loggin', true)
      if (state.isLogg) {
        // router.push({ name: 'me' })
        console.log(document.cookie)
      }
      commit('userData', res.data)
      commit('loggInUser', state.isLogg)
    }).catch((err) => {
      localStorage.setItem('loggin', false)
      console.log('Wrong!', err)
    })
  },
  logOut ({ commit }) {
    axios.post('https://mix.sumdu.edu.ua/api/v1/party/logout', {
      headers: { 'X-Requested-With': 'XMLHttpRequest', 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' }
    }).then((res) => {
      state.isLogg = false
      localStorage.setItem('loggin', false)
      commit('loggInUser', state.isLogg)
    })
  }
}

const getters = {
  isLoggIn: state => {
    return state.loggIn
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}



